/* COLORS */
/* FONTS */
/* COLORS */
/* FONTS */
* {
  margin: 0;
  box-sizing: border-box;
  appearance: none;
  font-family: "Outfit-Light";
  font-size: 30px;
  color: #1F314F;
  line-height: 1.5;
  cursor: url("/src/icons/cursor/cursor.svg") 0 0, auto !important;
}

*::-webkit-scrollbar {
  display: none;
}

/* COLORS */
/* FONTS */
.page {
  background-color: #D5E1EF;
}
.page__wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 27px;
}

.card {
  background-color: #FFFFFF;
  border-radius: 20px;
  padding: 16px;
  width: 100%;
  max-width: 320px;
  margin: 0 auto;
  box-shadow: 0px 25px 25px rgba(0, 0, 0, 0.0476518);
}
.card__image {
  width: 100%;
  display: block;
  margin-bottom: 24px;
  border-radius: 10px;
}
.card__title {
  font-family: "Outfit-Bold";
  text-align: center;
  font-size: 22px;
  margin-bottom: 16px;
}
.card__text {
  color: #7D889E;
  font-size: 15px;
  text-align: center;
}